import SocialMediaIcons from "../components/SocialMediaIcons";
import useMediaQuery from "../hooks/useMediaQuery";
import { motion } from "framer-motion";
import AnchorLink from "react-anchor-link-smooth-scroll";
import profile from '../assets/profile-pic6.png'
import { MdOutlineKeyboardArrowRight } from "react-icons/md";
import { Link } from "react-scroll";


const Landing = ({ setSelectedPage }) => {
  const isAboveLarge = useMediaQuery("(min-width: 1060px)");
  return (
   
    
     <div
      name="home"
      className="h-screen w-full "
    >
      <div className="max-w-screen-lg mx-auto flex flex-col items-center justify-between mt-1 h-full px-4 md:flex-row">
      <div className="z-10 flex sm:mt-4 md:order-2 ">
            {isAboveLarge ? (
         
            <img
              alt="profile"
              className="hover:filter hover:saturate-200 transition duration-500 z-10 w-full mb-5 max-w-[300px] md:max-w-[300px]"
              src={profile}
            />
         
        ) : (
          <img
            alt="profile"
            className="z-10 w-full mt-14 max-w-[300px] md:max-w-[300px]"
            src={profile}
          />
        )}
            </div>
        <div className="flex flex-col justify-center order-1 mt-4 h-full">
            <p>Hello my name is,</p>
          <h2 className="text-4xl sm:text-4xl font-bold text-white">
          Mohammad Sahil
          </h2>
            <h2 className="text-4xl sm:text-4xl font-bold text-white">
                I am a full stack developer
                </h2>

          <div>
            <div>
                <SocialMediaIcons/>
            </div>
            <Link
              to="portfolio"
              smooth
              duration={500}
              className="group text-white w-fit px-6 py-3 mt-2 flex items-center rounded-md bg-yellow cursor-pointer"
            >
           Resume
              <span className="group-hover:rotate-90 duration-300">
                <MdOutlineKeyboardArrowRight size={25} className="ml-1" />
              </span>
            </Link>
          </div>
        </div>

        
      </div>
    </div>


   
  );
};

export default Landing;
